import React from 'react';
import PropTypes from 'prop-types';
import { Form, Input, Button, Label } from 'semantic-ui-react';

const SignUpForm = ({ onChange, formValues, handleSubmit, validateError = false }) => {
	return (
		<Form action="#" onSubmit={(e) => handleSubmit(e, 'reg')}>
			<Form.Group widths='equal'>
				<Form.Field>
					<label htmlFor="loginPhone">Мобильный телефон (Логин)</label>
					<Input
						name="loginPhone"
						value={formValues.loginPhone || ''}
						onChange={onChange}
						label={{ content: '+375' }}
						labelPosition='left'
						placeholder='291234567'
					/>
				</Form.Field>
				<Form.Field control={Input} value={formValues.companyName || ''} onChange={onChange} name="companyName" label='Название организации' placeholder='ООО Моя Компания' />
			</Form.Group>
			<Form.Group widths='equal'>
				<Form.Field control={Input} value={formValues.directorName || ''} onChange={onChange} name="directorName" label='ФИО руководителя' placeholder='Петров Иван Иванович' />
				<Form.Field control={Input} value={formValues.buhName || ''} onChange={onChange} name="buhName" label='ФИО бухгалтера' placeholder='Петрова Елена Александровна' />
			</Form.Group>
			<Form.Group widths='equal'>
				<Form.Field control={Input} value={formValues.phone || ''} onChange={onChange} name="phone" label='Телефон' placeholder='375171234567' />
				<Form.Field control={Input} value={formValues.fax || ''} onChange={onChange} name="fax" label='Телефон/факс' placeholder='375171234567' />
			</Form.Group>
			<Form.Field type="password" onChange={onChange} name="password" control={Input} label='Пароль' placeholder='Введите пароль' />
			<Form.Field type="password" onChange={onChange} name="secondPassword" control={Input} label='Проверка Пароля' placeholder='Повторите пароль' />
			{validateError &&
			<Label basic color='red' pointing>{validateError.map(
				(error, i) => <div key={i}>{error}</div>
			)}</Label>}
			<Button
				type='submit'
				className="signUpButton"
				positive
				icon='signup'
				labelPosition='right'
				content={'Регистрация'}
				style={{ display: 'block' }}
			/>
		</Form>
	);
};

SignUpForm.propTypes = {
	onChange: PropTypes.func.isRequired,
	formValues: PropTypes.object.isRequired,
	handleSubmit: PropTypes.func.isRequired,
};

export default SignUpForm;
