import React from 'react';
import PropTypes from 'prop-types';
import { Form, Input, Button, Label } from 'semantic-ui-react';

const LoginForm = ({ onChange, formValues, handleSubmit, validateError = false }) => {
	return (
		<div>
			<Form action="#" onSubmit={(e) => handleSubmit(e, 'login')}>
				{validateError && <Label basic color='red' pointing='below'>{validateError}</Label>}
				<Form.Field>
					<label htmlFor="loginPhone">Мобильный телефон (Логин)</label>
					<Input name="loginPhone" value={formValues.loginPhone || ''} onChange={onChange} label={{ content: '+375' }} labelPosition='left' placeholder='291234567' />
				</Form.Field>
				<Form.Field>
					<label htmlFor="password">Пароль</label>
					<Input name="password" type="password" onChange={onChange} placeholder='Введите пароль' />
				</Form.Field>
				<Button
					type='submit'
					className="signUpButton"
					positive
					icon='send'
					labelPosition='right'
					content={'Войти в личный кабинет'}
				/>
			</Form>
		</div>
	);
};

LoginForm.propTypes = {
	onChange: PropTypes.func.isRequired,
	formValues: PropTypes.object.isRequired,
	handleSubmit: PropTypes.func.isRequired,
};

export default LoginForm;
